.movie-title {
    word-break: break-all;
}
.movie-item {
    margin: 1rem .5rem;
    max-width: 20rem;
}
.movie-info {
    padding: 5px 5px 0 5px;
    height: 11%;
    text-align: center;
    background-color: rgb(41, 41, 48);
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
}
.poster {
    height: 90%;
    padding: auto;
    background-color:  rgb(41, 41, 48);
}

.poster>img {
    height: 100%;
    width: 100%;
}